import React from 'react';
import tel from './assets/tel.png';
import it from './assets/it.png';
import inf from './assets/inf.png';
import en from './assets/ener.png';


import './style.css';

function index() {

    window.addEventListener('scroll', (e) =>{
        let scroll = document.body.getBoundingClientRect().top;
        console.log(scroll)
        if(scroll > -650) {
            const col1 = document.querySelector('.col1');
            const col3 = document.querySelector('.col3');
            col1.style.transform = `translateY(${100 + scroll*0.155}px)`
            col3.style.transform = `translateY(${100 + scroll*0.155}px)`
        }
    })

    return (
        <div id="section">
                <div className="squareGroup">
                    <div className="col1">
                        <div>
                            <h4>Sucesso</h4>
                            <p>A receita  simples:</p>
                            <p>Somos apaixonados por pessoas e por tecnologia.</p>
                            <p>Os nossos resultados so o reflexo disso.</p>
                        </div>
                        <div className="jobs">
                            <h4>React.js</h4>
                            <p> <i class="las la-map-marker"></i> Porto</p>
                            <p> <i class="las la-clock"></i> Full-time</p>
                        </div>
                    </div>
                    <div className="center">
                        <div className="squareTitle">
                            <div >
                                <h4>14 anos.</h4>
                                <h4>Obrigado!</h4>
                            </div>
                            <div className="minifooter">
                                <div className="line"/>
                                <p>Ver Oportunidades de Emprego (214)</p>
                            </div>
                        </div>
                        <div className="resume">
                            <p>
                                O pilar da PrimeIT so pessoas que nela trabalham e o valor que criamos para os nossos clientes. Mais de 14 anos de experincia permitem-nos oferecer as melhores solues, atravs de uma equipa de 1900 Primers que nos representam diariamente.
                            </p>
                        </div>
                    </div>
                    <div className="col3">
                        <div className="call">
                            <h4>Criamos uma poderosa equipa para o seu projecto</h4>
                            <div className="minifooter">
                                <div className="line"/>
                                <p>Faz a tua equipa</p>
                            </div>
                        </div>
                    </div>
                   
                </div>
                <div id="Area">
                    <div className="featuresText">
                        <h3>As nossas reas de actividade</h3>
                        <div className="line" />
                        <p>A experincia adquirida nas reas de actividade onde actuamos, permite-nos fazer face a qualquer tipo de desafio. Conhea o que fazemos.</p>
                    </div>
                    <div className="icons">
                        <div>
                            <img src={tel} alt="Telecom" />
                            <p>
                                TELECOM
                            </p>
                        </div>
                        <div>
                            <img src={it} alt="IT" />
                            <p>
                                IT
                            </p>
                        </div>
                        <div>
                            <img src={en} alt="Energy" />
                            <p>ENERGIA</p>
                        </div>
                        <div>
                            <img className="infra" src={inf} alt="Infra" />
                            <p>
                                INFRAESTRUTURAS
                            </p>
                        </div>
                    </div>
                    </div>
            
        </div>
    )
}

export default index



